<template>
    <w-form :model="form" ref="form" class="invoice__form" @submit="submit">
        <form-item label="" prop="FpType" v-model="form.FpType" required>
            <!-- <w-radio v-model="form.FpType" :data="fpData"></w-radio> -->
            <select class="weui-select" v-model="form.FpType" placeholder="">
                <option v-for="item in fpData" :value="item.value">{{item.label}}</option>
            </select>
        </form-item>
        <form-item label="" placeholder="" prop="DwMc" v-model="form.DwMc" required></form-item>
        <form-item label="" placeholder="" prop="XinYongCode" v-model="form.XinYongCode" required></form-item>
        <form-item label="" placeholder="" prop="Address" v-model="form.Address" required></form-item>
        <form-item label="" placeholder="" prop="DianHua" v-model="form.DianHua" required></form-item>
        <form-item label="" placeholder="" prop="KaiHuHang" v-model="form.KaiHuHang" required></form-item>
        <form-item label="" placeholder="" prop="YinHangZhangHao" v-model="form.YinHangZhangHao" required></form-item>
        <form-item label="" placeholder="" prop="Je" v-model="form.Je">
            {{form.Je}} 
        </form-item>
        <!-- <form-item label="" placeholder="" prop="LxrAddress" v-model="form.LxrAddress" required></form-item> -->
        <form-item label="" placeholder="" prop="Email" v-model="form.Email" required mail></form-item>
        <form-item label="" placeholder="" prop="Detail" v-model="form.Detail"></form-item>


        <div class="weui-btn-area" slot="btn" slot-scope="scope">
            <btn type="primary" @click="scope.submit"></btn>
        </div>
    </w-form>
</template>

<script>
export default {
    data () {
        return {
            form: {
                FpType: '',
                DwMc: '',
                XinYongCode: '',
                Address: '',
                DianHua: '',
                KaiHuHang: '',
                YinHangZhangHao: '',
                Je: '',
                LxrAddress: '',
                Detail: '',
            },
            fpData: [
                // {label:'',value:''},
                {label:'',value:''}
            ]
        }
    },
    methods:{
        submit() {
            var that = this;
            that.$post('/Api/FaPiao/AddFaPiao', that.form, function(data){
                app.ShowMsgBox('', function(){
                    that.$router.go(-1);
                })
            })
        },
        getInvoiceInfo() {
            this.form.Je = this.$store.state.invoiceInfo.FkJe;
            this.form.OrderGuid = this.$store.state.invoiceInfo.OrderGuid;
        }
    },
    mounted:function(){

    },
    activated() {
        this.getInvoiceInfo();
    },
    deactivated() {
        this.form = {
            FpType: '',
            DwMc: '',
            XinYongCode: '',
            Address: '',
            DianHua: '',
            KaiHuHang: '',
            YinHangZhangHao: '',
            Je: '',
            LxrAddress: '',
            Detail: '',
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    form.invoice__form{padding:0;}
</style>



